$black: #000;
$white: #fff;
$black0: #0E0E10;
$black1: #17171A;
$black2: #1F1F23;
$black3: #28282D;
$black4: #39393E;
$black50rgba: rgba(14, 14, 16, 0.7);
$grey0: #C9C9CB;
$grey1: #AFAFB2;
$grey2: #797983;
$grey3: #61616F;
$green: #37AD6D;
$red: #D52C2C;
$gradientRed: linear-gradient(227.8deg, #FB6F43 3.56%, #B41431 92.44%);
$gradientHover: linear-gradient(270deg, #28282D 0%, #1F1F23 100%);
$gradientMatch: linear-gradient(180deg, rgba(14, 14, 16, 0.82) 0%, #1F1F23 100%);
$gradientMatchLive: linear-gradient(180deg, rgba(219, 36, 36, 0) 65.62%, rgba(219, 36, 36, 0.2) 100%), linear-gradient(180deg, rgba(14, 14, 16, 0.82) 0%, #1F1F23 100%);
$borderRadius4: 4px;
$borderRadius50: 50%;

body {
	min-width: 340px;
	margin: 0;
	padding: 0;
	font-family: 'Roboto', sans-serif;
	color: $grey1;
	font-size: 15px;
	line-height: 1.5;
	letter-spacing: 0.5px;
	box-sizing: border-box;
	background: $black0;
}

iframe {
	width: 100%
}

.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0px;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden;
}

*, *:before, *:after {
	box-sizing: border-box;
	outline: none;
}

ul,
ol {
	list-style: none;
	margin: 0;
	padding: 0;
}

button {
	color: $grey1;
	border-radius: $borderRadius4;
	background: transparent;
	display: flex;
	align-items: center;
	justify-content: center;
}

a {
	color: $grey1;
	text-decoration: none;
}

p {
	margin: 0;
}

button {
	padding: 0;
	border: none;
	cursor: pointer;
}

.bg-main-block {
	padding: 32px;
	border-radius: 4px;
	background: $black1;
}

.title-global {
	font-weight: 400;
	font-size: 30px;
	line-height: 140%;
	letter-spacing: 0.15px;
	color: $grey0;
	margin: 0;
	margin-top: 16px;
}

.subtitle-global {
	font-weight: 500;
	font-size: 20px;
	line-height: 140%;
	letter-spacing: 0.15px;
	color: $grey0;
	padding: 14px 20px;
	&:hover {
    color: $white;
  }
}

.btn-global {
	display: flex;
	justify-content: center;
	align-items: center;
	background: $black2;
	padding: 0 14px;
	height: 32px;
	border: none;
	border-radius: 4px;
	font-size: 13px;
	line-height: 150%;
	letter-spacing: 0.25px;
	color: $grey2;
	&:hover {
		background: $black3;
		color: $white;
		svg path {
			fill: $white;
		}
	}
}

.text-color--green {
	color: $green
}

.text-color--red {
	color: $red;
}

.text-clip,
.text-clip-row {
	overflow: hidden;
	text-overflow: ellipsis;
}
	
.text-clip {
	width: 100%;
	white-space: nowrap;
}

.font-bold {
	font-weight: bold;
}

.list-mark-global {
	position: relative;
	padding-left: 12px;
	&::before {
		content: '';
		position: absolute;
		top: calc(50% - 2px);
		left: 0;
		width: 4px;
		height: 4px;
		border-radius: 50%;
		background: $grey3;
	}
}

.gradient-btn {
	background: $gradientRed;
	border-radius: 2px;
	font-size: 12px;
	font-weight: bold;
	letter-spacing: 1.5px;
	text-transform: uppercase;
	color: $white;
	padding: 0 6px;
}

@media (max-width: 900px) {
	.bg-main-block {
		padding: 15px;
	}
}
