.header {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding-right: 16px;
  background: $black2;
  box-shadow: 0px 2px 2px $black0;
  height: 64px;
  margin-bottom: 24px;
  &__list {
    display: flex;
  }
  &__item {
    position: relative;
    display: flex;
    align-items: center;
    margin-left: 12px;
    &--none {
      display: none;
    }
  }
  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    min-width: 40px;
    border-radius: $borderRadius4;
    font-size: 13px;
    letter-spacing: 0.25px;
    &:hover {
      background: $black3;
    }
    &:hover svg path {
      fill: $white;
    }
    &--mail {
      position: relative;
      &::before {
        content: '13';
        position: absolute;
        top: 0;
        right: 0;
        padding: 0 3px;
        font-weight: 500;
        font-size: 10px;
        line-height: 13px;
        color: $white;
        background: $gradientRed;
        border-radius: $borderRadius4;
      }
    }
    &--user {
      position: relative;
      border-radius: $borderRadius50;
      height: 100%;
      &--active:before {
        content: '';
        position: absolute;
        top: calc(50% - 20px);
        left: 0;
        width: 40px;
        height: 40px;
      }
      &:hover::before {
        background: url(../img/header/eclipse.svg) no-repeat;
      }
      &:hover {
        background: transparent;
      }
      &:hover ~ .header__sublist--active {
        display: block;
      }
    }
    &--exit {
      padding: 0 6px;
      svg {
        margin-right: 8px;
      }
    }
  }
  &__sublist {
    display: none;
  }
  &__sublist--active {
    position: absolute;
    top: 100%;
    right: 0;
    min-width: 246px;
    background: $black2;
    box-shadow: 0px 0px 10px rgba(14, 14, 16, 0.5);
    border-radius: $borderRadius4;
    padding: 8px 10px;
    padding-top: 12px;
    z-index: 11;
    &:hover {
      display: block;
    }
    &::before {
      content: '';
      position: absolute;
      bottom: 100%;
      left: 0;
      width: 100%;
      height: 20px;
      background: transparent;
    }
  }
  &__subitem-head {
    display: flex;
    align-items: center;
    padding-bottom: 8px;
    border-bottom: 1px solid $black3;
    margin-bottom: 8px;
    a {
      display: flex;
      align-items: center;
    }
    div {
      margin-left: 8px;
      display: flex;
      flex-direction: column;
    }
  }
  &__subitem-text--name {
    font-size: 13px;
    font-weight: bold;
    letter-spacing: 0.25px;
    &:hover {
      color: $white;
    }
  }
  &__subitem-text {
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0.3px;
  }
  &__subitem {
    display: flex;
  }
  &__sublink {
    display: flex;
    align-items: center;
    width: 100%;
    font-size: 13px;
    padding: 8px 5px;
    border-radius: $borderRadius4;
    svg {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 15px;
      margin-right: 8px;
    }
    &:hover {
      background: $black3;
      color: $white;
    }
    &:hover svg path {
      fill: $white;
    }
  }
}

@media (max-width: 700px) {
  .header {
    &__logo {
      display: flex;
      align-items: center;
      img {
        width: 120px;
      }
    }
  }
}