.forum-profile {
  &__head {
    display: flex;
    align-items: flex-start;
    &-img {
      width: 150px;
      border-radius: $borderRadius4;
    }
    &-block {
      margin-left: 30px;
      display: flex;
      flex-direction: column;
      width: calc(100% - 180px);
      min-height: 150px;
    }
    &-title {
      margin: 0;
    }
    &-rank {
      font-size: 16px;
      font-weight: 500;
      color: $white;
    }
    &-status {
      color: $grey3
    }
    &-list {
      display: flex;
      justify-content: space-between;
      width: 100%;
      margin-top: auto;
    }
    &-item {
      &-title {
        color: $grey2;
        text-transform: uppercase;
        font-size: 13px;
      }
      &-text {
        font-size: 13px;
      }
    }
  }
  &__content {
    margin-top: 24px;
    display: flex;
    &-block {
      border-radius: 0 0 $borderRadius4 $borderRadius4;
    }
  }
  &__right-bar {
    margin-left: 24px;
    width: 100%;
  }
  &__topblock {
    display: flex;
    background: $black1;
    border-radius: $borderRadius4 $borderRadius4 0 0;
    overflow: hidden;
    &-item {
      width: 25%;
    }
    &-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 56px;
      background: $black2;
      font-weight: 500;
      &:hover {
        background: transparent;
        color: $white;
      }
    }
  }
}