.news-page {
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__content {
    width: calc(100% - 324px);
    .index__news-list {
      margin-top: 0;
      margin-top: 24px;
    }
    .index__news-item {
      width: 32%;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    margin-top: 15px;
    overflow: hidden;
  }
  &__item {
    margin-right: 8px;
    &--active a {
      background: $black3;
      color: $white
    }
    &:last-child {
      margin-right: 0
    }
  }
  &__search {
    display: flex;
    width: 100%;
    border-radius: $borderRadius4;
    margin-top: 24px;
    input,
    button {
      height: 40px;
      border: none;
    }
    input {
      width: calc(100% - 40px);
      padding: 5px 12px;
      background: $black2;
      border-radius: $borderRadius4 0px 0px $borderRadius4;
      font-size: 13px;
      color: $grey1;
      &:hover {
        background: $black3;
      }
      &::placeholder {
        color: $grey2;
      }
    }
    button {
      width: 40px;
      background: $black2;
      border-radius: 0px $borderRadius4 $borderRadius4 0px;
      &:hover {
        background: $black3;
        svg path {
          fill: $white;
        }
      }
    }
  }
}
@media (max-width: 1100px) {
  .news-page {
    .index__news-item {
      width: 48%;
    }
  }
}
@media (max-width: 940px) {
  .news-page {
    &__wrap {
      flex-direction: column;
    }
    &__content {
      width: 100%;
    }
    .index__news-item {
      width: 32%;
    }
    &__right-bar {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      .component-block {
        margin-top: 24px;
      }
    }
  }
}

@media (max-width: 800px) {
  .news-page {
    .index__news-item {
      width: 48%;
    }
  }
}
@media (max-width: 500px) {
  .news-page {
    .index__news-item {
      width: 100%;
    }
  }
}