.breadcrumbs {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  &__item {
    position: relative;
    margin-right: 20px;
    &:last-child::before {
      display: none;
    }
    &::before {
      content: '';
      position: absolute;
      top: calc(50% - 1.5px);
      right: -10px;
      width: 3px;
      height: 6px;
      background: url(../img/arrow-right.svg) no-repeat center;
    }
  }
  &__link {
    font-size: 13px;
    color: $grey2;
    &:hover {
      color: $white;
    }
  }
}