.memes {
  &__mem {
    display: flex;
    position: relative;
    img {
      width: 100%;
    }
  }
  &__btn {
    position: absolute;
    top: calc(50% - 20px);
    width: 40px;
    height: 40px;
    background: rgba($black3, .7);
    border-radius: $borderRadius4;
    &--left {
      left: 8px;
    }
    &--right {
      right: 8px;
      svg {
        transform: rotate(180deg)
      }
    }
    &:hover {
      background: $black3;
    }
  }
  &__user {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 20px;
    padding-bottom: 12px;
    .component-text-grey-13 {
      color: $grey2;
    }
    .memes__text {
      img {
        margin: 0 5px;
      }
      &--w {
        color: $grey1;
        margin-left: 4px;
      }
    }
    span {
      display: flex;
      align-items: center;
      img {
        margin-right: 8px;
      }
    }
  }
}