.forum-page {
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__main {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-right: 20px;
    margin-bottom: 24px;
  }
  &__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 16px;
    margin-bottom: 24px;
    h1 {
      margin-top: 0;
    }
  }
  &__section {
    margin-bottom: 24px;
    background: $black2;
    border-radius: $borderRadius4;
    &:last-child {
      margin-bottom: 0;
    }
    &-title {
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      border-bottom: 1px solid $black3;
      &:hover {
        color: $white;
      }
    }
    &-list {
      border-radius: 0 0 $borderRadius4 $borderRadius4;
    }
    &-item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 15px;
      border-bottom: 1px solid $black3;
      &:last-child {
        border: none;
      }
      a:hover {
        color: $white;
      }
      img {
        width: 36px;
        border-radius: $borderRadius50;
        margin-bottom: auto;
        margin-top: 5px;
      }
    }
    &-links {
      margin-left: 10px;
      margin-right: auto;
      &-title {
        display: block;
        font-size: 16px;
        line-height: 18px;
      }
      &-list {
        padding-left: 10px;
      }
      &-item {
        display: flex;
        margin-top: 8px;
      }
      &-link {
        font-size: 12px;
      }
    }
    &-block-text {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      text-align: center;
      color: $grey3;
      font-size: 12px;
      margin: 0 10px;
      &--mobile {
        display: none;
        color: $grey3;
        font-size: 12px;
      }
    }
    &-block-user {
      display: flex;
      font-size: 12px;
      min-width: 250px;
      p {
        color: $grey3;
      }
      div {
        margin-left: 10px;
      }
    }
  }
  &__right-bar {
    &-btn {
      width: 100%;
    }
    &-block {
      &-img {
        display: flex;
        align-items: center;
        justify-content: center;
      }
      .component-block__link:hover .component-text-grey-13 {
        color: $grey1;
      }
      .component-text-grey-11 .text-clip {
        max-width: 82px; 
      }
      .component-block__link .component-text-grey-13:hover,
      .component-block__link a:hover {
        color: $white;
      }
    }
    &-stat-block {
      margin-top: 10px;
      padding: 0 20px;
    }
    &-stat {
      display: flex;
      justify-content: space-between;
      font-size: 13px;
    }
  }
  &__users-online {
    &-title {
      font-size: 18px;
      font-weight: 400;
      margin: 0;
    }
    &-info {
      font-size: 14px;
    }
    &-list {
      margin-top: 15px;
      display: flex;
      flex-wrap: wrap;
    }
    &-item {
      display: flex
    }
    &-link {
      font-size: 13px;
    }
  }
}

@media (max-width: 1200px) {
  .forum-page {
    &__section-block-text {
      display: none;
    }
    &__section-block-text--mobile {
      display: block;
    }
  }
}

@media (max-width: 1000px) {
  .forum-page {
    &__wrap {
      flex-direction: column;
    }
    &__main {
      margin-right: 0;
    }
    &__right-bar {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: flex-start
    }
  }
}

@media (max-width: 650px) {
  .forum-page {
    &__section-block-user {
      display: none;
    }
  }
}