.forum-theme {
  &__block {
    margin-bottom: 2px;
  }
  &__top-block {
    &-wrap {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 10px;
      margin-bottom: 22px;
      a:hover {
        color: $white;
        svg path {
          fill: $white;
        }
      }
    }
    &-user,
    &-time,
    &-text {
      color: $grey2;
      font-size: 13px;
      display: flex;
      align-items: center;
    }
    &-user {
      font-weight: bold;
      img {
        width: 20px;
        border-radius: $borderRadius50;
        margin-right: 8px;
      }
    }
    &-time {
      margin-left: 14px;
      margin-right: auto;
      img {
        margin-right: 4px;
      }
    }
    &-info {
      display: flex;
      align-items: center;
    }
    &-text {
      display: flex;
      align-items: center;
      margin-left: 12px;
      svg {
        margin-right: 4px;
        max-width: 12px;
      }
      &--link svg path {
        fill: $grey2;
      }
    }
  }
  &__item {
    display: flex;
    min-height: 361px;
    &-block-info {
      display: none;
    }
    &,
    &-like-link {
      font-size: 13px;
      color: $grey2;
    }
    &-left {
      display: flex;
      flex-direction: column;
      margin-right: 24px;
      width: 120px;
    }
    &-name {
      display: block;
      font-weight: bold;
      font-size: 15px;
      color: $grey0;
      max-height: 46px;
      &:hover {
        color: $white;
      }
    }
    &-status {
      display: flex;
      align-items: center;
      span {
        width: 8px;
        height: 8px;
        background: $black4;
        border-radius: $borderRadius50;
        margin-right: 4px;
      }
    }
    &-avatar {
      width: 120px;
      border-radius: $borderRadius4;
      margin-top: 8px;
      margin-bottom: 2px;
      img {
        border-radius: $borderRadius4;
      }
    }
    &-info {
      width: 150px;
      font-size: 12px;
      transform: scale(0.8);
      transform-origin: 0 100%;
      &-desk {
        margin-bottom: 10px;
      }
    }
    &-block-achiv {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      margin-top: 10px;
      height: 32px;
      div {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        margin-right: 12px;
        &:last-child {
          margin-right: 0
        }
        img {
          width: 32px;
        }
        span {
          position: absolute;
          top: 6px;
          left: 0;
          width: 100%;
          text-align: center;
          font-weight: bold;
          letter-spacing: 0.25px;
          color: $white;
          text-shadow: 0px 2px 2px $black0;
        }
      }
    }
    &-time {
      display: flex;
      align-items: center;
      margin-bottom: 10px;
      img {
        margin-right: 4px;
      }
    }
    &-block-mess {
      display: flex;
      flex-direction: column;
      justify-content: center;
      p {
        font-size: 15px;
        color: $grey1;
        margin-bottom: 17px;
      }
    }
    &-block-remess {
      position: relative;
      padding-left: 10px;
      border-left: 2px solid $black4;
      margin-bottom: 10px;
      max-height: 80px;
      overflow: hidden;
      &-user {
        margin-bottom: 5px;
        a {
          font-weight: bold;
          color: $grey2;
        }
        svg {
          transform: rotate(90deg);
          margin-left: 11px;
          path {
            fill: $grey2;
          }
        }
        a:hover {
          color: $white;
          svg path {
            fill: $white;
          }
        }
      }
      &-btn {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        margin: 0 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 24px;
        background: linear-gradient(360deg, #17171A 0%, rgba(23, 23, 26, 0.72) 100%);
        svg {
          transform: rotate(-90deg);
        }
        &:hover svg path {
          fill: $white;
        }
      }
    }
    &-bottom {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: auto;
    }
    &-right {
      display: flex;
      flex-direction: column;
      width: 100%;
    }
    &-like {
      &-list {
        display: flex;
        align-items: center;
      }
      &-item {
        margin-right: 8px;
        &:last-child {
          margin-right: 0
        }
      }
      &-link {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 12px;
        height: 32px;
        min-width: 32px;
        padding: 0 4px;
        background: $black2;
        border-radius: $borderRadius4;
        svg,
        img {
          margin-right: 4px;
        }
        &:hover {
          background: $black3;
          color: $white;
          svg path {
            fill: $white;
          }
        }
        &--like,
        &--dislike {
          padding: 0 12px;
          svg {
            margin-right: 6px;
          }
        }
        &--like {
          &:hover {
            color: $green;
            svg path {
              stroke: $green;
              fill: none;
            }
          }
        }
        &--dislike {
          svg {
            transform: rotate(180deg);
          }
          &:hover {
            color: $red;
            svg path {
              stroke: $red;
              fill: none;
            }
          }
        }
        &--plus svg {
          margin: 0;
        }
      }
    }
    &-dots {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 22px;
      height: 22px;
      margin-left: auto;
      margin-right: 10px;
      cursor: pointer;
      span::before,
      span::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
      }
      span {
        position: relative;
      }
      span,
      span::before,
      span::after {
        width: 3px;
        height: 3px;
        background: $grey2;
        border-radius: $borderRadius50;
      }
      span::before {
        transform: translateX(5px);
      }
      span::after {
        transform: translateX(-5px);
      }
      &:hover {
        span,
        span::before,
        span::after {
          background: $white;
        }
      }
      &-block {
        display: none;
        position: absolute;
        bottom: 100%;
        right: 0;
        flex-direction: column;
        background: $black2;
        padding: 7px 10px;
        border-radius: $borderRadius4;
        a {
          display: flex;
          align-items: center;
          font-size: 12px;
          letter-spacing: 0.3px;
          color: $grey2;
          padding: 3px 2px;
          border-radius: $borderRadius4;
          svg {
            margin-right: 3px;
          }
          &:hover {
            color: $white;
            background: $black3;
            svg path {
              fill: $white;
            }
          }
        }
      }
      &:hover .forum-theme__item-dots-block {
        display: flex;
      }
    }
    &-btn {
      display: flex;
      align-items: center;
      color: $grey2;
      letter-spacing: 0.25px;
      padding: 6px 12px;
      background: $black2;
      border-radius: $borderRadius4;
      svg {
        margin-right: 8px;
      }
      &:hover {
        color: $white;
        background: $gradientRed;
        svg path {
          fill: $white;
        }
      }
    }
  }
  &__info-view {
    margin: 24px 0;
    p {
      color: $grey2;
      border-bottom: 1px solid $black3;
      padding-bottom: 3px;
    }
    a {
      font-weight: bold;
      font-size: 13px;
      &:hover {
        color: $white;
      }
    }
  }
  &__form {
    background: $black2;
    border-radius: $borderRadius4;
    padding: 12px;
    margin-top: 16px;
    textarea {
      width: 100%;
      padding: 8px;
      margin-bottom: 8px;
      background: transparent;
      border: none;
      color: $grey1;
      line-height: 150%;
      letter-spacing: 0.5px;
      resize: none;
      overflow: hidden;
      &::placeholder {
        color: $grey2;
      }
    }
    &-block {
      display: flex;
      a {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 40px;
        height: 40px;
        margin-right: 8px;
        &:hover svg path {
          fill: $white;
        }
      }
      button {
        display: flex;
        align-items: center;
        margin-left: auto;
        border: none;
        font-weight: bold;
        font-size: 12px;
        line-height: 14px;
        letter-spacing: 0.3px;
        height: 40px;
        padding: 0 20px;
        svg {
          margin-left: 5px;
        }
        &:hover {
          background: $gradientRed;
          color: $white;
          svg path {
            fill: $white;
          }
        }
      }
    }
  }
}

@media (max-width: 900px) {
  .forum-theme {
    &__item {
      flex-direction: column;
      &-left {
        flex-direction: row;
        width: auto;
        margin: 0;
        margin-bottom: 8px;
      }
      &-left-mob {
        order: 1;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        margin-left: 10px;
        .forum-theme__item-name {
          margin-right: 8px;
        }
      }
      &-block-info {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        p {
          width: auto;
          margin-right: -8px;
        }
      }
      &-block-achiv,
      &-info-desk {
        display: none;
      }
      &-avatar {
        width: 50px;
        margin: 0;
        img {
          width: 50px;
        }
      }
    }
  }
}

@media (max-width: 600px) {
  .forum-theme {
    &__top-block {
      &-wrap {
        flex-wrap: wrap;
      }
      &-info {
        width: 100%;
        margin-top: 10px;
      }
      &-text {
        margin-left: 0;
        margin-right: 12px;
      }
    }
    &__item {
      &-bottom {
        flex-wrap: wrap;
      }
      &-like-list {
        width: 100%;
        margin-bottom:10px;
      }
      &-dots {
        margin: 0;
        &-block {
          right: auto;
          left: 0;
        }
      }
    }
  }
}

@media (max-width: 410px) {
  .forum-theme {
    &__item {
      &-like-item:nth-child(3),
      &-like-item:nth-child(4),
      &-like-item:nth-child(5) {
        display: none;
      }
    }
  }
}