.component-block {
  background: $black2;
  border-radius: $borderRadius4;
  margin-top: 24px;
  width: 300px;
  min-width: 300px;
  &:first-child {
    margin-top: 0;
  }
  &:last-child {
    margin-bottom: 24px;
  }
  &__title {
    display: flex;
    &:hover {
      color: $white;
    }
  }
  &__list {
    border-top: 1px solid $black3;
    padding-bottom: 12px;
    li:first-child .component-block__block {
      border-top: none;
    }
  }
  &__link {
    display: flex;
    align-items: center;
    padding: 0 20px;
    padding-right: 10px;
    &:hover {
      background: $gradientHover;
      .component-text-grey-13 {
        color: $white;
      }
    }
  }
  &__img {
    margin-right: 10px;
    &--width-26 {
      width: 26px;
    }
    &--width-36 {
      width: 36px;
    }
    &--width-42 {
      width: 42px;
    }
    &--radius {
      border-radius: $borderRadius50;
    }
  }
  &__block {
    display: flex;
    flex-direction: column;
    border-top: 1px solid $black3;
    padding: 10px 0;
    width: 100%;
  }
}

.component-text-grey-13 {
  font-size: 13px;
  letter-spacing: 0.25px;
  max-width: 220px
}

.component-text-grey-11 {
  font-size: 11px;
  letter-spacing: 0.3px;
  color: $grey3;
  max-width: 220px
};

@media (max-width: 665px) {
  .component-block {
    width: 100%;
    min-width: 300px;
  }
}