.news-news {
  display: flex;
  &__main {
    margin-right: 24px;
  }
  h2,
  h3  {
    font-weight: 500;
    font-size: 20px;
    line-height: 140%;
    letter-spacing: 0.15px;
    color: $grey0;
    margin-top: 32px;
    margin-bottom: 20px;
  }
  h3 {
    font-size: 17px;
    line-height: 135%;
    letter-spacing: 0.25px;
    margin-bottom: 8px;
  }
  &__block-preview {
    p > a {
      color: $grey2;
      text-decoration: underline;
    }
    i > img {
      width: 100%;
      margin-top: 32px;
    }
    ol,
    ul {
      margin-bottom: 32px;
      li {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-bottom: 4px;
        &:last-child {
          margin-bottom: 0;
        }
        span {
          margin-right: 8px;
          color: $grey2;
        }
        a {
          display: flex;
          align-items: center;
          margin-right: 8px;
          min-height: 23px;
          &:nth-child(2) {
            color: $grey2;
            text-decoration: underline;
          }
          img {
            display: flex;
            width: 16px;
            margin: 0;
            margin-right: 4px;
          }
        }
      }
    }
    ul {
      li {
        span {
          width: 4px;
          height: 4px;
          border-radius: $borderRadius50;
          background: $grey2;
        }
      }
    }
    blockquote {
      padding: 28px;
      margin: 0;
      background: $gradientHover;
      border-radius: $borderRadius4;
      & > div {
        display: flex;
        align-items: flex-start;
        p {
          font-size: 17px;
          margin-bottom: 15px;
          &:last-child {
            margin-bottom: 0;
          }
        }
      }
      img {
        width: 40px;
        margin: 0;
        margin-right: 12px;
      }
      a {
        display: flex;
        align-items: flex-start;
        margin-top: 16px;
        color: $grey2;
        p {
          font-size: 13px;
        }
      }
    }
    u {
      text-decoration: none;
      color: $grey2;
      font-size: 13px;
      letter-spacing: 0.25px;
      a {
        color: $grey2;
        font-weight: bold;
      }
    }
    dl {
      margin: 0;
      margin-top: 20px;
      dt,
      dd {
        position: relative;
        margin-left: 0;
        padding-left: 24px;
        &:before {
          content: '';
          position: absolute;
          top: 11px;
          left: 0;
          width: 16px;
          height: 1px;
          background: $grey3;
        }
      }
      dt {
        margin-bottom: 8px;
        p {
          font-weight: 500;
          font-size: 17px;
          line-height: 135%;
          letter-spacing: 0.25px;
          color: $grey0;
        }
      }
      dd {
        margin-bottom: 20px;
        p {
          margin-bottom: 16px;
          &:last-child {
            margin-bottom: 0;
          }
        }
      }
    }
  }
  &__tabel-version-1,
  &__tabel-version-2 {
    margin-top: 12px;
    margin-bottom: 20px;
    background: $black2;
    width: 100%;
    border-collapse: collapse;
    th,
    td {
      border: 1px solid $black1;
    }
  }
  &__tabel-version-1 {
    th,
    td {
      height: 44px;
      text-align: center;
    }
    th {
      background: $black3;
      font-weight: bold;
    }
    td:hover {
      background: $black3;
    }
  }
  &__tabel-version-2 {
    td {
      padding: 30px;
      b {
        display: flex;
        align-items: center;
        justify-content: center;
      }
      ul {
        margin: auto;
        margin-top: 12px;
        li {
          a {
            &:nth-child(2) {
              text-decoration: none;
              color: $grey1;
            }
          }
        }
      }
    }
  }
  &__slider {
    &-top {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 410px;
      background: $black0;
      border-radius: $borderRadius4 $borderRadius4 0 0;
      img {
        display: flex;
        width: auto;
        margin: 0;
        max-height: 410px;
      }
      button {
        position: absolute;
        top: calc(50% - 20px);
        width: 40px;
        height: 40px;
        background: $black2;
        opacity: 0.7;
        border-radius: $borderRadius4;
        &:hover {
          opacity: 1;
        }
      }
    }
    &-btn--left {
      left: 20px;
    }
    &-btn--right {
      right: 20px;
      transform: rotate(180deg);
    }
    &-bottom {
      display: flex;
      align-items: center;
      background: $black2;
      border-radius: 0 0 $borderRadius4 $borderRadius4;
      padding: 20px;
      span {
        margin-right: 20px;
        color: $grey3;
      }
      p {
        font-size: 13px;
      }
    }
  }
  &__bottom-block {
    &-poll {
      div {
        padding: 20px 32px;
        width: 48%;
        display: flex;
        flex-direction: column;
        background-image: $gradientMatch, url(../img/news-news/img-slider.jpg);
        background-repeat: no-repeat;
        background-size: cover;
        border-radius: $borderRadius4;
        h3,
        u {
          margin: 0;
          text-align: center;
          margin: 0 auto;
        }
        u {
          margin-top: 12px;
        }
        label {
          display: flex;
          align-items: center;
          margin-top: 12px;
          height: 38px;
          padding: 0 12px;
          border-radius: $borderRadius4;
          background-color: $black1;
          cursor: pointer;
          font-size: 13px;
          letter-spacing: 0.25px;
          color: #C9C9CB;
          span {
            display: none;
            margin-left: 4px;
          }
          svg {
            display: none;
            margin-left: auto;
          }
          &:hover {
            background-image: $gradientHover;
            svg {
              display: block;
            }
          }
        }
        input:checked ~ label {
          color: $white;
          background-image: linear-gradient(90deg, #28282D 0%, #28282D 46%, rgba(0, 0, 0, 0) 46%, rgba(0, 0, 0, 0) 100%);
        }
        input:checked ~ label span {
          display: block;
          color: $white;
        }
        #news-news-radio-1:checked ~ label[for=news-news-radio-1],
        #news-news-radio-2:checked ~ label[for=news-news-radio-2],
        #news-news-radio-3:checked ~ label[for=news-news-radio-3],
        #news-news-radio-4:checked ~ label[for=news-news-radio-4],
        #news-news-radio-5:checked ~ label[for=news-news-radio-5],
        #news-news-radio-6:checked ~ label[for=news-news-radio-6] {
          background-image: linear-gradient(90deg, #39393E 0%, #39393E 46%, rgba(0, 0, 0, 0) 46%, rgba(0, 0, 0, 0) 100%), $gradientHover;
          svg {
            display: block;
            path {
              fill: $white;
            }
          }
        }
      }
    }
    & > div {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      margin-top: 24px;
      margin-bottom: 12px;
    }
    ul {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      margin: 0;
      li {
        margin-right: 8px;
        margin-bottom: 8px;
        &:last-child {
          margin-bottom: 8px;
        }
        button {
          padding: 0 12px;
          p {
            margin-left: 4px;
          }
          svg {
            margin-right: 4px;
          }
        }
        .btn-global--social {
          padding: 0;
          width: 32px;
        }
      }
    }
    .news-news__bottom-block-like {
      flex-wrap: nowrap;
      li {
        button {
          &:hover {
            color: $green;
            svg path {
              fill: transparent;
              stroke: $green;
            }
          }
        }
        &:last-child {
          button {
            svg {
              transform: scale(1, -1);
            }
            &:hover {
              color: $red;
              svg path {
                stroke: $red;
              }
            }
          }
        }
      }
    }
  }
  &__news-list li {
    width: 32%;
  }
  &__block-comment {
    background: $black1;
    border-radius: $borderRadius4;
    padding: 14px 20px;
    padding-bottom: 55px;
    &-top {
      display: flex;
      align-items: flex-start;
      h2 {
        margin: 0;
      }
      div {
        display: flex;
        margin-left: 44px;
        button {
          margin-right: 12px;
          height: 28px;
          padding: 0 8px;
        }
      }
      a {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 28px;
        padding: 0 8px;
        margin-left: auto;
        font-size: 13px;
        letter-spacing: 0.25px;
        color: $grey2;
        svg {
          margin-right: 4px;
        }
        &:hover {
          color: $white;
          svg path {
            fill: $white;
          }
        }
      }
    }
  }
}
