.navigation {
  display: flex;
  margin-right: auto;
  margin-left: 8px;
  &__list {
    display: flex;
    &--mobile {
      display: none;
    }
  }
  &__item {
    position: relative;
    &:hover .navigation__sublist {
      display: block;
    }
    &--mobile {
      display: none;
    }
  }
  &__link {
    justify-content: center;
    padding: 0 16px;
  }
  &__link,
  &__sublink {
    display: flex;
    font-weight: 500;
    align-items: center;
    border-radius: $borderRadius4;
    height: 100%;
    &:hover {
      background: $black3;
      color: $white;
    }
  }
  &__sublist {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    padding: 10px;
    background: $black2;
    box-shadow: 0px 0px 10px rgba(14, 14, 16, 0.5);
    border-radius: 0px 0px $borderRadius4 $borderRadius4;
    min-width: 246px;
    z-index: 10;
  }
  &__sublink {
    padding: 11px 10px;
  }
}

@media (max-width: 1170px) {
  .navigation {
    &__list--mobile {
      //////////////////////////////////////////////////////////////////////////////////////
      // display: block;
      //////////////////////////////////////////////////////////////////////////////////////
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      background: #1f1f23;
      z-index: 10;
      .navigation__link {
        justify-content: flex-start;
        text-align: left;
        width: 100%;
        padding: 15px 10px;
        border-top: 1px solid $black0;
      }
      .navigation__item {
        display: none;
      }
      .navigation__item:nth-child(8) {
        display: block;
      }
      .navigation__sublist {
        display: block;
        position: relative;
        padding: 0;
        box-shadow: none;
      }
      .navigation__sublink {
        height: auto;
        padding: 2px 15px;
        font-size: 13px
      }
    }
    &__item--mobile {
      display: flex;
      span {
        position: relative;
        width: 2px;
        height: 2px;
        border-radius: $borderRadius50;
        background: $grey1;
        &::before,
        &::after {
          content: '';
          position: absolute;
          top: calc(50% - 1px);
          right: calc(50% - 1px);
          width: 2px;
          height: 2px;
          border-radius: $borderRadius50;
          background: $grey1;
        }
        &::before {
          transform: translateX(5px);
        }
        &::after {
          transform: translateX(-5px);
        }
      }
    }
    &__item:nth-child(8){
      display: none;
    }
  }
}

@media (max-width: 1120px) {
  .navigation {
    &__item:nth-child(7) {
      display: none;
    }
    &__list--mobile {
      .navigation__item:nth-child(7) {
        display: block;
      }
    }
  }
}

