.forum {
  &__list {
    padding-bottom: 0;
  }
  &__link {
    &--arrow {
      padding: 10px 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      svg {
        transform: rotate(-90deg);
        path {
          fill: $grey2;
        }
      }
      &:hover {
        svg path {
          fill: $white;
        }
      }
    }
  }
  &__block {
    .component-text-grey-11 {
      display: flex;
      align-items: center;
      span {
        margin-left: 3px;
      }
    }
    img {
      margin-left: 8px;
      margin-right: 5px;
    }
  }
  &__text {
    color: $grey1;
  }
}