.pagination {
  display: flex;
  align-items: center;
  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 4px;
  }
  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    padding: 6px 9px;
    height: 30px;
    color: $grey2;
    background: $black2;
    border-radius: $borderRadius4;
    &:hover {
      color: $white;
      svg path {
        fill: $white;
      }
    }
    &--active {
      background: $gradientRed;
      color: $white;
    }
    &--right svg {
      transform: rotate(180deg);
      margin-bottom: 1px;
    }
    span {
      margin: 0 4px;
    }
  }
}

@media (max-width: 600px) {
  .pagination {
    &__item {
      &--desk {
        display: none;
      }
    }
  }
}

@media (max-width: 400px) {
  .pagination {
    &__item {
      &--all {
        display: none;
      }
    }
  }
}