.footer {
  background: $black2;
  margin-top: 47px;
  &__top {
    padding: 32px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__first,
  &__list,
  &__social {
    display: flex;
    align-items: center;
  }
  &__first {
    a {
      display: flex;
      margin-right: 8px;
    }
    div {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 37px;
      height: 30px;
      background: $black3;
      border-radius: $borderRadius4;
      font-size: 12px;
      color: $grey2;
    }
  }
  &__list {
    margin-right: auto;
    margin-left: 42px;
  }
  &__item {
    margin-right: 20px;
  }
  &__link {
    font-size: 13px;
    letter-spacing: 0.25px;
    color: $grey2;
    &:hover {
      color: $grey1;
    }
  }
  &__social {
    li {
      margin-left: 8px;
    }
    &-link {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 32px;
      height: 32px;
      background: $black2;
      border-radius: $borderRadius4;
      &:hover {
        svg path {
          fill: $white;
        }
      }
      &--1:hover {
        background: #45668E;
      }
      &--2:hover {
        background: #0088CC;
      }
      &--3:hover {
        background: #1DA1F2;
      }
    }
  }
  &__bottom {
    background: rgba(14, 14, 16, 0.5);
    &-wrap {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 10px 0;
    }
    p {
      font-size: 12px;
      color: $grey2;
    }
    a {
      display: flex;
      align-items: center;
      font-size: 13px;
      color: $grey2;
      &:hover {
        color: $grey1;
      }
      img {
        margin: 0 5px;
      }
    }
  }
}

@media (max-width: 950px) {
  .footer {
    &__top {
      flex-direction: column;
      justify-content: center;
    }
    &__first {
      order: 3;
    }
    &__list {
      margin: auto
    }
    &__social {
      margin: 20px 0;
    }
  }
}

@media (max-width: 650px) {
  .footer {
    &__list {
      flex-direction: column;
    }
    &__item {
      margin: auto;
    }
    &__bottom-wrap {
      flex-direction: column-reverse;
      p {
        margin-top: 6px;
      }
    }
  }
}