.authorization {
  display: none;
  position: fixed;
  top: calc(50% - 194px);
  left: calc(50% - 175px);
  width: 350px;
  background: $black1;
  border-radius: $borderRadius4;
  padding-bottom: 32px;
  z-index: 100;
  &__overlay {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(14, 14, 16, 0.7);
    z-index: 99;
  }
  &--active,
  &__overlay--active {
    display: block;
  }
  &__btn-close {
    position: absolute;
    top: 0;
    right: 0;
    width: 40px;
    height: 40px;
    border: none;
    background: transparent;
    &:before,
    &:after {
      content: '';
      position: absolute;
      top: calc(50% - 8px);
      left: calc(50% - 1px);
      width: 2px;
      height: 16px;
      background: $grey2;
      border-radius: 2px;
    }
    &:before {
      transform: rotate(45deg);
    }
    &:after {
      transform: rotate(-45deg);
    }
    &:hover:before,
    &:hover:after {
      background: $white;
    }
  }
  &__title {
    font-weight: 500;
    font-size: 17px;
    line-height: 135%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    letter-spacing: 0.25px;
    color: $grey0;
    margin: 0;
    background: $black2;
    border-radius: $borderRadius4 $borderRadius4 0px 0px;
  }
  &__form {
    padding: 32px 20px;
    display: flex;
    flex-direction: column;
  }
  &__input {
    height: 40px;
    padding: 12px;
    background: $black2;
    border: 1px solid $black1;
    border-radius: $borderRadius4;
    margin-bottom: 12px;
    color: $grey1;
    &::placeholder {
      color: $grey2;
    }
    &:hover {
      background: $black3;
      border-color: $black4;
    }
    &:focus {
      background: $black3;
      border-color: $grey3;
    }
  }
  &__checkbox {
    display: flex;
    align-items: center;
    margin-bottom: 12px;
    font-size: 13px;
    letter-spacing: 0.25px;
    color: $grey2;
    cursor: pointer;
    &-label {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 22px;
      height: 22px;
      background: $black2;
      border: 1px solid $black1;
      border-radius: $borderRadius4;
      margin-right: 8px;
      svg {
        display: none;
      }
    }
    &:hover {
      color: $grey1;
    }
    &:hover .authorization__checkbox-label {
      background: $black3;
      border-color: $black4;
      svg {
        display: block;
      }
    }
    &-input:checked ~ .authorization__checkbox {
      color: $grey1;
      span {
        background: $gradientRed;
        border: none;
        svg {
          display: block;
          path {
            fill: $white;
          }
        }
      }
    }
  }
  &__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    background: $black3;
    border-radius: $borderRadius4;
    border: none;
    font-weight: bold;
    font-size: 12px;
    letter-spacing: 0.3px;
    color: $grey1;
    svg {
      margin-left: 6px;
    }
    &:hover {
      background: $gradientRed;
      color: $white;
      svg path {
        fill: $white;
      }
    }
    &:active {
      background: $red;
      color: $white;
      svg path {
        fill: $white;
      }
    }
  }
  &__social {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
    a {
      display: flex;
      align-items: center;
      justify-content: center;
      width: calc(50% - 6px);
      height: 40px;
      border-radius: $borderRadius4;
      font-size: 12px;
      letter-spacing: 0.3px;
      color: $white;
      opacity: .8;
      svg {
        margin-right: 4px;
        margin-bottom: 1px;
        path {
          fill: $white;
        }
      }
      &:hover {
        opacity: 1;
      }
    }
    &--vk {
      background: #45668E;
      svg {
        width: 18px;
      }
    }
    &--steam {
      background: $black0;
    }
  }
  &__question {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 12px;
    span {
      font-weight: bold;
      font-size: 12px;
      margin: 0 5px;
      color: $black4;
    }
    a {
      font-size: 13px;
      letter-spacing: 0.25px;
      color: $grey2;
      &:hover {
        color: $white;
      }
    }
  }
}

@media (max-width: 400px) {
  .authorization {
    width: 100%;
  }
}