.matches {
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__title {
    padding: 0;
  }
  &__text {
    display: flex;
    align-items: center;
    color: $grey2;
    font-size: 13px;
    letter-spacing: 0.25px;
    margin-bottom: 0px;
    margin-right: -90px;
    svg {
      margin-right: 12px;
    }
  }
  &__toggle {
    display: flex;
    align-items: center;
    &-left,
    &-right {
      width: 40px;
      height: 40px;
      background: $black2;
      border-radius: $borderRadius4;
      &:hover {
        background: $black0;
        svg path {
          fill: $grey2;
        }
      }
    }
    &-right svg {
      transform: rotate(180deg);
    }
    &-dots {
      margin: 0 5px;
      border-radius: $borderRadius50;
      width: 8px;
      height: 8px;
      background: $black2;
      &--active {
        background: $black4;
      }
    }
  }
  &__more {
    display: none;
  }
  &__slider {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 16px;
  }
  &__slide {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 19%;
    height: 116px;
    padding: 0 20px;
    background-image: $gradientMatch, url(../img/matches/bcg.jpg);
    background-size: cover;
    &--live::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0; 
      width: 100%;
      height: 100%;
      background: $gradientMatchLive;
    }
    &-left,
    &-middle,
    &-right {
      display: flex;
      flex-direction: column;
      align-items: center;
      font-weight: 500;
      font-size: 13px;
      z-index: 1;
    }
    &-middle {
      width: 52px;
      margin: 0 2px;
    }
    &-left,
    &-right {
      width: calc(50% - 26px);
      text-align: center;
      img {
        width: 32px;
        margin-top: 3px;
        margin-bottom: 5px;
      }
      .text-clip {
        margin-bottom: 2px;
      }
    }
    &-middle {
      align-self: flex-start;
    }
    &-dota,
    &-cup {
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &-dota {
      width: 24px;
      height: 24px;
      background: rgba(14, 14, 16, 0.7);
      border-radius: 0px 0px $borderRadius4 $borderRadius4;
      &:hover {
        background: rgba(14, 14, 16, 0.3);
        svg path {
          opacity: .5
        }
      }
    }
    &-cup {
      width: 30px;
      height: 30px;
      background: $black3;
      border-radius: $borderRadius4;
      margin-top: 19px;
      margin-bottom: 11px;
      &:hover {
        background: $black2;
        svg path {
          fill: $grey1;
        }
      }
    }
    &-coeff {
      font-size: 12px;
      letter-spacing: 1.5px;
      margin: 0;
      margin-top: 4px;
      padding: 2px 4px;
      border-radius: $borderRadius4;
      transform: scale(0.8);
      &--green {
        background: rgba(55, 173, 109, 0.05);
      }
      &--red {
        background: rgba(219, 36, 36, 0.05);
      }
    }
    &-live {
      margin: 21px 0;
      margin-top: 19px;
      transform: scale(0.7);
    }
    &-time {
      text-align: center;
      font-weight: 500;
      font-size: 13px;
      margin-top: 11px;
      margin-bottom: 12px;
      span {
        display: block;
        font-weight: 400;
        font-size: 11px;
        letter-spacing: 0.3px;
        color: $grey2;
        margin-top: -2px;
      }
    }
  }
}

@media (max-width: 1100px) {
  .matches {
    &__slide {
      width: 24%;
      &:nth-child(5) {
        display: none;
      }
    }
  }
}

@media (max-width: 880px) {
  .matches {
    &__slide {
      width: 32%;
      &:nth-child(1) {
        display: none;
      }
    }
  }
}

@media (max-width: 670px) {
  .matches {
    &__toggle,
    &__text {
      display: none;
    }
    &__title {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 14px 0;
      width: 100%;
      background: $black2;
      border-radius: $borderRadius4 $borderRadius4 0px 0px;
    }
    &__slider {
      margin-top: 0;
      flex-direction: column;
    }
    &__slide {
      width: 100%;
      border-top: 1px solid $black3;
    }
    &__more {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 10px 0;
      background: $black2;
      border-radius: 0px 0px $borderRadius4 $borderRadius4;
      font-size: 13px;
      &:hover {
        color: $white;
        svg path {
          fill: $white;
        }
      }
      svg {
        margin-left: 8px;
        transform: rotate(-90deg);
      }
    }
  }
}