.banner {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  margin-bottom: 24px;
  border-radius: $borderRadius4;
  padding: 0 15px;
  img {
    border-radius: $borderRadius4;
  }
}

@media (max-width: 900px) {
  .banner img {
    width: 100%;
  }
}